}\r\n    }\r\n}\r\n\r\n//Col spans\r\n@mixin colSpans($count, $size) {\r\n    @for $i from 1 through $count {\r\n        .col_span_#{$size}_#{$i}{\r\n            grid-column: span #{$i}\r\n        }\r\n    }\r\n}\r\n\r\n@include singleProp(12, pt, padding-top, def);\r\n@include singleProp(12, mt, margin-top, def);\r\n@include singleProp(12, col_span, grid-column, def);\r\n@include colSpans(12, def);\r\n\r\n@include grid-cols(def);\r\n\r\n@media (min-width: 599px) {\r\n    @include grid-cols(sm);\r\n    .center_sm{\r\n        justify-content: center;\r\n    }\r\n}\r\n@media (min-width: 904px) {\r\n    @include grid-cols(md);\r\n    .center_md{\r\n        justify-content: center;\r\n    }\r\n}\r\n@media (min-width: 1239px) {\r\n    @include grid-cols(lg);\r\n    .center_lg{\r\n        justify-content: center;\r\n    }\r\n}\r\n\r\n.flex{\r\n    display: flex;\r\n}\r\n\r\n.jcc{\r\n    justify-content: center;\r\n}\r\n\r\n.aic{\r\n    align-items: center;\r\n}\r\n\r\n.grid{\r\n    display: grid;\r\n}\r\n\r\n.col_s5{\r\n    grid-column: span 5;\r\n}\r\n\r\n.col_s2{\r\n    grid-column: span 2;\r\n}\r\n\r\n.empty{\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n}","$primary_blue: #004A87;\r\n\r\n.primary_theme{\r\n    background-color: $primary_blue;\r\n    color: white;\r\n}\r\n\r\n.green{\r\n    color: green;\r\n    font-weight: 600;\r\n}\r\n\r\n.blue{\r\n    color: $primary_blue;\r\n    font-weight: 600;\r\n}\r\n\r\n.red{\r\n    color: red;\r\n    font-weight: 600;\r\n}\r\n\r\n.yellow{\r\n    color: rgb(199, 199, 0);\r\n    font-weight: 600;\r\n}\r\n\r\n.orange{\r\n    color: orange;\r\n    font-weight: 600\r\n}\r\n\r\n.warning_theme{\r\n    background-color: #ff682d;\r\n    color: rgb(255, 255, 255);\r\n}\r\n\r\n.success_theme {\r\n    background-color: green;\r\n    color: rgb(255, 255, 255);\r\n}\r\n\r\n.error_theme{\r\n    background-color: rgb(255, 0, 0);\r\n    color: white;\r\n}\r\n\r\n.txtWhite{\r\n    color: white;\r\n}\r\n\r